import React, {useEffect, useState} from 'react';
import { DislikeFilled, DislikeOutlined, LikeFilled, LikeOutlined } from '@ant-design/icons';
import 'antd/dist/antd.css';
import './show.css';
import axios from 'axios';
import {List, Avatar, message, Input, Comment, Tooltip, Button, Form, Modal, Layout} from "antd";
import VirtualList from 'rc-virtual-list';
import moment from "moment";
import Account from "../account/account";
const {TextArea} = Input;
const { Header, Content, Footer, Sider } = Layout;



const ContainerHeight = 400;

const Editor = ({ onChange, onSubmit, submitting, value }) => (
    <>
        <Form.Item>
            <TextArea rows={4} onChange={onChange} value={value} />
        </Form.Item>
        <Form.Item>
            <Button htmlType="submit" loading={submitting} onClick={onSubmit} type="primary">
                Add Comment
            </Button>
        </Form.Item>
    </>
);


const Show = () => {

    /**
     * videoId获取
     * @type {string}
     */
    let videoId = '';
    function GetUrlPara(){
        const url = document.location.toString();//获取当前URL
        if (url.indexOf("?") !== -1) {  //判断URL？后面不为空
            let arrUrl = url.split("?");//分割？
            let para = arrUrl[1];//获取参数部分
            //console.log(para)
            if(para)//判断传入的参数部分不为空
            {
                let arr = para.split("=");//分割=
                //console.log(arr);
                videoId = arr[1];//获取参数的值
            }
        }
        return null;
    }
    GetUrlPara();

    /**
     * 回复评论
     */
    const [isModalVisible, setIsModalVisible] = useState(false);
    const [parentId, setParentId] = useState('');
    //回复输入框
    const showModal = (id) => {
        setParentId(id);
        console.log(parentId)
        setIsModalVisible(true);
    };
    //模态框点击ok
    const handleOk = () => {
        let url = '/comments_insert?parentId='+parentId+'&commentText='+valueOfReply;
        console.log(url)
        axios.get(url).then(res=>{
                console.log('res=>',res);
            })
        setValueOfReply('');
        setIsModalVisible(false);
    };
    //模态框点击cancel
    const handleCancel = () => {
        setIsModalVisible(false);
    };
    //对顶级评论进行回复
    const [valueOfReply, setValueOfReply] = useState('');
    const replySubmit = (e) => {
        if (!value) return;
        let data = new FormData();
        //data.append('parentId',e.target.parentId);
        console.log(e)
        data.append('commentText',value);
        let url = '/commentThreads_insert';
        /*axios.post(url,data)
            .then(res=>{
                console.log('res=>',res);
            })*/
        setValueOfReply('');
    };
    const valueOfReplyChange = (e) => {
        setValueOfReply(e.target.value);
    };


    /**
     * 评论数据
     */
    const [data, setData] = useState();
    const appendData = () => {
        let url = '/show?videoId=' + videoId;
        axios.get(url).then(({data}) => {         //这里的{data}相当于res.data
            setData(data.items);
            console.log(data.items)
        })
    };
    useEffect(() => {
        appendData();
    }, []);


    //todo 踩顶操作没有完善
    /**
     * 对视频的rate(点赞和点踩)
     */
    const [likes, setLikes] = useState(0);
    const [dislikes, setDislikes] = useState(0);
    const [action, setAction] = useState(null);
    useEffect(() => {
        let url = '/getRating?videoId=' + videoId;
        axios.get(url).then(({data}) => {         //这里的{data}相当于res.data
            console.log(data)
            //setAction(data.items);
            //console.log(data.items)
        })
    }, []);
    //顶操作
    const like = () => {
        let url = '/videos_rate?id=' + videoId + '&rating=like';
        axios.get(url).then(({data}) => {
            console.log(data)
        })
        setLikes(1);
        setDislikes(0);
        setAction('liked');
    };
    //踩操作
    const dislike = () => {
        let url = '/videos_rate?id=' + videoId + '&rating=dislike';
        axios.get(url).then(({data}) => {
            console.log(data)
        })
        setLikes(0);
        setDislikes(1);
        setAction('disliked');
    };


    /**
     * 发表顶级评论
     */
    const [value, setValue] = useState('');
    const [submitting, setSubmitting] = useState(false);
    const handleSubmit = () => {
        if (!value) return;
        setSubmitting(true);
        setTimeout(() => {
            let data = new FormData();
            data.append('videoId',videoId);
            data.append('commentText',value);
            let url = '/commentThreads_insert';
            axios.post(url,data)
                .then(res=>{
                    console.log('res=>',res);
                })
            setSubmitting(false);
            setValue('');
        }, 1000);
    };
    const handleChange = (e) => {
        setValue(e.target.value);
        console.log(e);
    };


    /**
     * 根据顶级评论Id获取顶级评论的回复
     */
    const [childComments, setChildComments] =useState('');
    const getChildComments = (id) => {
        let url = '/getChildComments?parentId=' + id;
        axios.get(url).then(({data}) => {         //这里的{data}相当于res.data
            setChildComments(data.items);
            console.log("子评论=>" + data.items)
        })

    }


    /**
     * 控制侧边栏
     */
    const [collapsed, setCollapsed] = useState(false);

    return(
        <Layout
            style={{
                minHeight: '100vh',
            }}
        >
            <Sider collapsible collapsed={collapsed} onCollapse={(value) => setCollapsed(value)}>
                <Account></Account>
            </Sider>
            <Layout>
                <>
        <div className='iframe_bg'>
            <iframe width="1400" height="720" src={"https://www.youtube.com/embed/" + videoId}/>
        </div>
        <div className="rate_bg">
            <Tooltip key="comment-basic-like" title="Like">
                                      <span onClick={like}>
                                        {React.createElement(action === 'liked' ? LikeFilled : LikeOutlined)}
                                          {/*<span className="comment-action">{likes}</span>*/}
                                      </span>
            </Tooltip>,
            <Tooltip key="comment-basic-dislike" title="Dislike">
                                        <span onClick={dislike}>
                                            {React.createElement(action === 'disliked' ? DislikeFilled : DislikeOutlined)}
                                            {/*<span className="comment-action">{dislikes}</span>*/}
                                    </span>
            </Tooltip>
        </div>
        <div className='comment_bg'>
            {/*// todo 还没有获取用户的信息，如头像地址*/}
            <Comment
                avatar={<Avatar src="https://joeschmoe.io/api/v1/random" alt="Han Solo" />}
                content={
                    <Editor
                        onChange={handleChange}
                        onSubmit={handleSubmit}
                        submitting={submitting}
                        value={value}
                    />
                }
            />
        </div>

        <div className='commentThread_bg'>
            <List
                dataSource={data}
                renderItem={(item, index) => (
                    <List.Item key={item.id}>
                        <Comment
                            actions={[
                                /*<Tooltip key="comment-basic-like" title="Like">
                                      <span onClick={like}>
                                        {React.createElement(action === 'liked' ? LikeFilled : LikeOutlined)}
                                          <span className="comment-action">{item.snippet.topLevelComment.snippet.likeCount}</span>
                                      </span>
                                </Tooltip>,
                                <Tooltip key="comment-basic-dislike" title="Dislike">
                                        <span onClick={dislike}>
                                            {React.createElement(action === 'disliked' ? DislikeFilled : DislikeOutlined)}
                                            <span className="comment-action">{dislikes}</span>
                                    </span>
                                </Tooltip>,*/
                                <span key="comment-basic-reply-to" onClick={() => {showModal(item.id)}}>Reply to({item.snippet.totalReplyCount})</span>,
                                <span key="comment-basic-reply-to" /*onClick={getChildComments(item.id)}*/>查看回复</span>,
                                ]
                            }
                            /*//todo 动态渲染回复评论的输入框 目前就是传入数据然后给一个模态框*/
                            children={[]}
                            author={<a href={item.snippet.topLevelComment.snippet.authorChannelUrl}>{item.snippet.topLevelComment.snippet.authorDisplayName}</a>}
                            avatar={<Avatar src={item.snippet.topLevelComment.snippet.authorProfileImageUrl} alt="Han Solo" />}
                            content={
                                <p>
                                    {item.snippet.topLevelComment.snippet.textOriginal}
                                </p>
                            }
                            datetime={
                                <Tooltip title={moment(item.snippet.topLevelComment.snippet.publishedAt).format('YYYY-MM-DD HH:mm:ss')}>
                                    <span>{moment(item.snippet.topLevelComment.snippet.publishedAt).fromNow()}</span>
                                </Tooltip>
                            }
                        />

                    </List.Item>
                )}
            >
            </List>
        </div>
            <Modal title="Basic Modal" visible={isModalVisible} onOk={handleOk} onCancel={handleCancel}>
                <TextArea rows={4} onChange={valueOfReplyChange} value={valueOfReply} />
            </Modal>
        </>
            </Layout>
        </Layout>
    )
}


export default Show;
