import React, {useEffect, useState} from 'react';
import 'antd/dist/antd.css';
import './personalChannel.css';
import axios from 'axios';
import {Form, Input, List, Avatar, Space, Checkbox, Button} from "antd";
import { LikeOutlined, MessageOutlined, StarOutlined } from '@ant-design/icons';

const {Search} = Input;





const Channel = () => {
    const [channelInfo, setChannelInfo] = useState()
    useEffect(() => {
        let url = '/getChannel';
        axios.get(url).then(({data}) => {         //这里的{data}相当于res.data
            console.log(data)
            setChannelInfo(data)
            console.log(channelInfo)
        })

    }, []);

    const onFinish = (values) => {
        let url = '/editChannels?id=' + channelInfo.items[0].id + '&description=' + values.description;
        axios.get(url).then(({data}) => {         //这里的{data}相当于res.data
            console.log(data)
        })
        console.log('Success:', values);
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const ChannelInfo = () => {
        return channelInfo &&
            <>
                <p>channelTitle: {channelInfo.items[0].snippet.title}</p>
                <p>channelDescription: {channelInfo.items[0].snippet.description}</p>
            </>
    }



    return(
        <>
        <div className='channel_bg'>
            <ChannelInfo/>
        </div>


        <Form
            className='form_bg'
            name="basic"
            labelCol={{
                span: 5,
            }}
            wrapperCol={{
                span: 16,
            }}
            initialValues={{
                remember: true,
            }}
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
            autoComplete="off"
        >
            <Form.Item
                label="description"
                name="description"
                rules={[
                    {
                        required: true,
                        message: 'Please input your description!',
                    },
                ]}
            >
                <Input />
            </Form.Item>


            <Form.Item
                wrapperCol={{
                    offset: 8,
                    span: 16,
                }}
            >
                <Button type="primary" htmlType="submit">
                    Submit
                </Button>
            </Form.Item>
        </Form>
        </>
    );
};

export default Channel;
